.markdown{
  line-height: 1.625;
  font-size: 1.125rem
}

.markdown h1,.markdown h2{
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  font-weight: 700
}

.markdown h3,.markdown h4,.markdown h5,.markdown h6{
  font-size: 1.25rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  font-weight: 600
}

.markdown a{
  --text-opacity: 1;
  color: #3182ce;
  color: rgba(49, 130, 206, var(--text-opacity))
}

.markdown a:hover{
  text-decoration: underline
}

.markdown p{
  margin-bottom: 1rem
}

.markdown ul,.markdown ol{
  margin-bottom: 1rem;
  margin-left: 2rem
}

.markdown li>p,.markdown li>ul,.markdown li>ol{
  margin-bottom: 0
}

.markdown ol{
  list-style-type: decimal
}

.markdown ul{
  list-style-type: disc
}

.markdown blockquote{
  padding: 0;
  padding: 0.5rem;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  --bg-opacity: 1;
  background-color: #1a202c;
  background-color: rgba(26, 32, 44, var(--bg-opacity));
  margin-bottom: 1rem;
  border-left-width: 4px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
  font-style: italic
}

.markdown blockquote>p{
  margin-bottom: 0
}

.markdown td,.markdown th{
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  border-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity))
}

.markdown tr:nth-child(odd){
  --bg-opacity: 1;
  background-color: #f7fafc;
  background-color: rgba(247, 250, 252, var(--bg-opacity))
}

.markdown table{
  margin-bottom: 1.5rem;
  --bg-opacity: 1;
  background-color: #1a202c;
  background-color: rgba(26, 32, 44, var(--bg-opacity))
}

